// Angular
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
// Components
import { BaseComponent } from './base/base.component';
import { ErrorPageComponent } from './content/error-page/error-page.component';
// Auth
import { AuthGuard } from '../../../core/auth';

// import { UpgradeComponent } from './../../pages/upgrade/upgrade.component';
//import { DevicesComponent } from './../../pages/tacacs/devices/devices.component';

const routes: Routes = [
	{
		path: '',
		component: BaseComponent,
		canActivate: [AuthGuard],
		children: [
			{
				path: '',
				loadChildren: 'app/views/pages/dashboard/dashboard.module#DashboardModule'
			},
			{
				path: 'tacacs',
				loadChildren: 'app/views/pages/tacacs/tacacs.module#TacacsModule'
			},
			{
				path: 'radius',
				loadChildren: 'app/views/pages/radius/radius.module#RadiusModule'
			},
			{
				path: 'mavis',
				loadChildren: 'app/views/pages/mavis/mavis.module#MavisModule'
			},
			{
				path: 'reports',
				loadChildren: 'app/views/pages/reports/reports.module#ReportsModule'
			},
			{
				path: 'gui',
				loadChildren: 'app/views/pages/gui/gui.module#GuiModule'
			},
			{
				path: 'confmanager',
				loadChildren: 'app/views/pages/confmanager/confmanager.module#ConfmanagerModule'
			},
			// {
			// 	path: 'upgrade',
			// 	component: UpgradeComponent
			// },
			{path: 'error/:type', component: ErrorPageComponent},
			{path: '', redirectTo: '', pathMatch: 'full'},
			{path: '**', redirectTo: '', pathMatch: 'full'},
		]
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class PagesRoutingModule {
}
