// Angular
import { AfterViewInit, Component, ElementRef, OnInit, ViewChild, Input } from '@angular/core';
import { NavigationCancel, NavigationEnd, NavigationStart, RouteConfigLoadEnd, RouteConfigLoadStart, Router } from '@angular/router';
// Object-Path
import * as objectPath from 'object-path';
// Loading bar
import { LoadingBarService } from '@ngx-loading-bar/core';
// Layout
import { LayoutRefService, LayoutConfigService } from '../../../../core/_base/layout';
// HTML Class Service
import { HtmlClassService } from '../html-class.service';
//Bootstrap Modal fot idle timeout
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
// RXJS
import { Observable, BehaviorSubject, timer } from 'rxjs';
import { take, map, tap } from 'rxjs/operators';
// NGRX
import { select, Store } from '@ngrx/store';
// State
import { AppState } from '../../../../core/reducers';
import { currentUser, Logout, AuthService, User } from '../../../../core/auth';

import {Idle, DEFAULT_INTERRUPTSOURCES} from '@ng-idle/core';
import {Keepalive} from '@ng-idle/keepalive';

@Component({
  selector: 'ngbd-modal-content',
  template: `
    <div class="modal-header">
      <h4 class="modal-title">Session Timeout Notification</h4>
      <button type="button" class="close" aria-label="Close" (click)="activeModal.dismiss('Cross click')">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
    <div class="modal-body" *ngFor="let counter of (counter$ | async)">
      <p>Your session is about to expire.</p>
			<p>Redirecting in {{counter}}s seconds.!</p>
			<p><ngb-progressbar type="brand" [value]="100 * counter / 60" [striped]="true" [animated]="false"></ngb-progressbar></p>
    </div>
    <div class="modal-footer">
			<button type="button" class="btn btn-default" (click)="logout()">Logout</button>
			<button type="button" class="btn btn-primary" (click)="activeModal.close('Close click')">Stay Connected</button>
    </div>
  `
})
export class NgbdModalContent {
	counter$: Observable<number[]>;
	count = 60;
	percentage = 100;

  constructor(
		public activeModal: NgbActiveModal,
		private store: Store<AppState>,
		private auth: AuthService
	) {
			this.counter$ = timer(0,1000).pipe(
				//tap( (data) => { if (data > 59) this.logout(); console.log(data, this.count)} ),
				take(this.count),
				map(() => {
					if (this.count <= 1) this.logout();
					return [--this.count]
				})
			);
	}

	logout(){
		this.activeModal.close('Close click')
		this.auth.logout().subscribe( data => {
			// console.log(data)
		})
		this.store.dispatch(new Logout());
	}
}


@Component({
	selector: 'kt-header',
	templateUrl: './header.component.html',
	styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit, AfterViewInit {
	//idle params
	idleState = 'Not started.';
	timedOut = false;
	lastPing?: Date = null;
	unsubscribe = new BehaviorSubject(false)
	// Public properties
	menuHeaderDisplay: boolean;
	@ViewChild('ktHeader') ktHeader: ElementRef;

	/**
	 * Component constructor
	 *
	 * @param router: Router
	 * @param layoutRefService: LayoutRefService
	 * @param layoutConfigService: LayoutConfigService
	 * @param loader: LoadingBarService
	 * @param htmlClassService: HtmlClassService
	 */
	constructor(
		private router: Router,
		private layoutRefService: LayoutRefService,
		private layoutConfigService: LayoutConfigService,
		public loader: LoadingBarService,
		public htmlClassService: HtmlClassService,
		private idle: Idle,
		private keepalive: Keepalive,
		private modalService: NgbModal
	) {
		// page progress bar percentage
		this.router.events.subscribe(event => {
			if (event instanceof NavigationStart) {
				// set page progress bar loading to start on NavigationStart event router
				this.loader.start();
			}
			if (event instanceof RouteConfigLoadStart) {
				this.loader.increment(35);
			}
			if (event instanceof RouteConfigLoadEnd) {
				this.loader.increment(75);
			}
			if (event instanceof NavigationEnd || event instanceof NavigationCancel) {
				// set page progress bar loading to end on NavigationEnd event router
				this.loader.complete();
			}
		});
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit(): void {
		const config = this.layoutConfigService.getConfig();

		// get menu header display option
		this.menuHeaderDisplay = objectPath.get(config, 'header.menu.self.display');

		// animate the header minimize the height on scroll down. to be removed, not applicable for default demo
		/*if (objectPath.get(config, 'header.self.fixed.desktop.enabled') || objectPath.get(config, 'header.self.fixed.desktop')) {
			// header minimize on scroll down
			this.ktHeader.nativeElement.setAttribute('data-ktheader-minimize', '1');
		}*/

		// sets an idle timeout of 5 seconds, for testing purposes.
		this.idle.setIdle(900);
		// sets a timeout period of 5 seconds. after 10 seconds of inactivity, the user will be considered timed out.
		this.idle.setTimeout(5);
		// sets the default interrupts, in this case, things like clicks, scrolls, touches to the document
		this.idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);
		// console.log(this.idle)

		// idle.onIdleEnd.subscribe(() => this.idleState = 'No longer idle.');
		this.idle.onTimeout.subscribe(() => {
			// console.log('Timed out!')
			const modalRef = this.modalService.open(NgbdModalContent);
			this.timedOut = true;
			this.reset()
		});
		// idle.onIdleStart.subscribe(() => this.idleState = 'You\'ve gone idle!');
		this.idle.onTimeoutWarning.subscribe((countdown) => {
			// console.log(countdown)
			this.idleState = 'You will time out in ' + countdown + ' seconds!'
		});

		// sets the ping interval to 15 seconds
		// keepalive.interval(15);

		// keepalive.onPing.subscribe(() => this.lastPing = new Date());

		this.reset();
	}

	reset() {
		this.idle.watch();
		this.idleState = 'Started.';
		this.timedOut = false;
	}

	/**
	 * On destroy
	 */
	ngOnDestroy(): void {
		// console.log('header destroy')
		// this.idle.stop();
	}

	ngAfterViewInit(): void {
		// keep header element in the service
		this.layoutRefService.addElement('header', this.ktHeader.nativeElement);
	}
}
